:root {
  --pf-chart-global--FontFamily: $font-family-base;
  --pf-global--FontSize--md: $font-size-base;
  --pf-global--FontSize--sm: 13px;
}

.odc-empty-state__hint-block {
  padding-bottom: 20px;
}

.odc-empty-state__title {
  background-color: var(--pf-global--BackgroundColor--light-100);
}

.odc-empty-state__content {
  padding: var(--pf-global--spacer--lg);
  flex: 1;
}

.odc-empty-state__tile {
  height: 100%;
  width: 260px;
}

